.header{
    text-align: center;
}
.logo{
    height: 90px;
}
.header nav{
    background-color: #EE276B;
    text-align: left;
    padding: 10px;
}
.header nav a {
    color: white;
    text-decoration: none;
    font-size: 20px;
    margin: 0 15px;
    border-bottom: 2px solid transparent;
    transition: .3s;
}
.header nav a:hover{
    border-bottom: 2px solid white;
}