.product{
    display: flex;
    border-bottom: 1px solid rgb(209, 207, 207);
    margin-right: 10px;
}
.product-name{
    color: rgb(73, 73, 73);
}
.btn{
    background: rgb(238, 39, 107);
    padding: 8px 30px;
    border: none;
    margin: 10px;
    border-radius: 5px;
    color: white;
    font-weight: 600;
    transition: .3s;
    border:  2px solid transparent;
}
.btn:hover{
    background: white;
    border: 2px solid rgb(238, 39, 107);
    color: rgb(73, 73, 73)
}